body, html {
  background: linear-gradient(to left, rgb(202, 175, 175), rgb(143, 150, 204));
  font-family: 'Open Sans', 'Roboto', 'Noto Sans JP', 'Ubuntu', sans-serif, Tahoma, Arial, Helvetica;
  color: rgb(57, 57, 57);
}
.navbar {
  background-color: burlywood;
}
.samer-logo {
  width: 50px;
  height: 50px;
}
.ss {
  font-size: 30px;
  margin-left: 10px;
}
.ss:hover {
  text-decoration: none;
  text-shadow: 4px 4px 7px rgb(143, 150, 204);
}
#selfie {
  width: 200px;
  height: 200px;
  padding: 2px;
  margin: 10px;
  border-radius: 10px;
  border: burlywood solid 3px;
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 48px;
  background-color: burlywood;
}

footer a {
  padding-left: 1.5em;
  padding-right: 1.5em;  
}

@media only screen and (max-width: 576px) {
  #selfie {
      display: flex;
      margin: 0 auto;
      float: none !important;
      justify-content: center;
      width: 200px;
      height: 200px;
      padding: 10px;
  }
  
  h1 {
     text-align: center;
  }
  .newClass { 
      display: flex;
      flex-direction: column;
  }
  
}

@media only screen and (min-width: 577px) {
  #selfie {
      float: left;
  }
}